<template>
	<view class="" style="position: relative;">
		<load ref="passwLoad"></load>
		<popUp ref="passwPopUp"></popUp>
		<view @click="goInputEmail" class="" style="position: absolute; top: 130rpx; left: 78rpx;"
		hover-class="hover1" hover-start-time=0 hover-stay-time=150 >
			<image src="../../static/Index/fanHui.png" mode="widthFix" style="width: 25rpx;"></image>
		</view>
		<image class="bgc-picture2" src="../../static/fristPage/bgcL.png" mode="widthFix"></image>
		<view class="" style="background-color: #2b95d0; position: fixed; bottom: 0; z-index: -2; height: 100vh; width: 750rpx;"></view>		
		<view class="" style="height: 290rpx;"></view> 
		<view class="bgc_FFF" style="margin: 0 78rpx; border-radius: 51rpx; height: 710rpx;">
			<view class="" style="height: 70rpx;"></view>
			<view class="color-H0f649c fw800 f49rpx ta-c" style="">
				Wrong Password 
			</view>
			<view class="" style="height: 30rpx;"></view>
			<view class="df-jcsb" style="align-items: center; height: 90rpx; border-radius: 15rpx; margin: 0 45rpx 30rpx; border: 1rpx solid #000000;">
				<view class="df-jcc-aic" style="margin: 0 30rpx 0 47rpx;">
					<image src="../../static/fristPage/suo_y.png" mode="widthFix" style="width: 26rpx;"></image>
				</view>
				<view class="df-jcc-aic">
					<input type="text" value="" placeholder="Password" :password="eyeFlag"
					placeholder-class="input1" v-model.trim="inputPassword" class="fw800 f28rpx"
					style="height: 90rpx; caret-color: #ee5b30; color: #007AFF;"/>
				</view>
				<view @click="switchDisplay" class="df-jcc-aic" style="margin: 0 47rpx 0 10rpx;">
					<image  :src="eyeFlag ? srcEye1 : srcEye2" mode="widthFix" style="width: 40rpx;"></image>
				</view>
			</view>
			<view class="f30rpx ta-c" style="margin: 0 60rpx 29rpx; color: #ee5b30;"> 
				That password doesn’t seem to be the right one! Try re-entering or select one of the options below.
			</view>
			<view @click="Login" class="fw800 ta-c bgc-o-r FFF noE box-s" style=""
			hover-class="hover_a24" hover-start-time=0 hover-stay-time=150  >
				Log IN
			</view>
			<view @click="goforgotPassword" class="fw800 ta-c bgc-o-r FFF noE box-s" style=""
			hover-class="hover_a24" hover-start-time=0 hover-stay-time=150 >
				Reset Password
			</view>
		</view>
	</view>
</template>

<script>
	export default{
		data(){
			return{
				eyeFlag:true,
				srcEye1:"../../static/fristPage/by_Y.png",
				srcEye2:'../../static/fristPage/zhenyan.png', 
				inputPassword:''
			}
		},
		methods:{
			// 切换显示密码
			switchDisplay(){
				this.eyeFlag = !this.eyeFlag
			},
			goInputEmail(){
				uni.redirectTo({
					url:'./inputEmail'
				})
			},
			async goforgotPassword(){
				let email = uni.getStorageSync('Email')
				const res = await this.$myRequest({
					url:'/api/auth/recover' + '?email=' + email,
				}) 
				console.log(res); 
				uni.redirectTo({ 
					url:'./forgotPassword'
				})
			},
			//登录
			async Login(){
				console.log(this.inputPassword);
				const email = uni.getStorageSync('Email')
				console.log(email);
				var _this = this
				if(_this.inputPassword != ''){
					_this.$refs.passwLoad.openLoad()
					const {data:res} = await this.$myRequest({
						method:'POST',
						url:'/api/auth/login',
						data:{
							"email":email,
							"password":_this.inputPassword
						}
					}) 
					console.log(res);
					if(res != ''){
						_this.$refs.passwLoad.closeLoad()
					} 
					if(res.succeeded){
						let token = res.data.token
						console.log(res.data.token);
						uni.setStorageSync('isLogin',true)
						uni.setStorageSync('token',token)
						uni.setStorageSync('Password',_this.inputPassword)
						console.log('密码正确');
						uni.switchTab({
							url:"../index/index"
						})
					}else{
						console.log('密码错误');
						_this.$refs.passwPopUp.openPopup(res.errmsg)   
					} 
					
				}else{ 
					this.$refs.passwPopUp.openPopup('Enter Password')
					return false
				}
				 
			},
		}
	}
</script>

<style>
	.noE{
		line-height: 84rpx; 
		letter-spacing: 2rpx; 
		height: 84rpx; 
		border-radius: 42rpx; 
		margin: 0 41rpx 20rpx;
	}
	.box-s{
		box-shadow: 0rpx 0rpx 5rpx #000;
	}
	.bgc-picture2{
		 z-index:-1; 
		 width:100%; 
		 height:100%; 
		 position: fixed;  
		 top:0; 
	}
	.fboxSha{
		box-shadow: 0rpx 0rpx 3rpx #000;
	}
	.input1{
		font-size: 21rpx;
		font-family: myfonts;
		font-weight: 800;
		color: #91a1aa;
	}
</style>
